}

.navbar-main .theme-mode {
    font-size: 28px;
    color: rgb(255,208,23);
    border-radius: 2rem;
    padding: 8px;
    margin: 0 0 -1.5rem 48%;
}
.navbar-main .theme-mode:hover {
    font-size: 28px;
    color: rgb(255,208,23);
    background-color: #fdfae5;
    border: 1px dashed rgb(255,208,23);
    transition: 0.4s ease-in-out;
}

.dropdown{
    position: relative;
    display: block;
}
.dropdown .menu-bars {
    color: #06D85F;
    font-size: 32px;
}
.dropdown .dropdown-button{
    padding: 0.5rem;
    margin: 1rem 0 0 -1rem;
    background-color: #ffffff;
    border: 2px solid #06D85F;
    border-radius: 1rem;
    cursor:pointer;
    align-self: flex-end;
    transition: 0.6s ease-in-out;
}
.dropdown .dropdown-content{
    display: none;
    position: absolute;
    right: 0;
    margin: -0.8rem 0 0;
    border: 2px solid #06D85F;
    background-color: #ffffff;
    min-width: 14rem;
    overflow: auto;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    border-radius: 20px 0;
    z-index: 1;
    /* margin-top: 12px; */
}

.dropdown .dropdown-menu{
    position: relative;
    margin: 0 0 0 0;
    background-color: #ffffff;
    border-radius: 0.5rem 0 0 0.5rem;
}
.dropdown-content li {
    width: 100%;
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: flex-start;
}
.dropdown-content .icon{
    width: 1.2rem;
    color: #9A7B4F;
    border: 2px solid #9A7B4F;
    padding: 0.5rem;
    margin: 0 0.5rem -0.8rem;
    position: relative;
    border-radius: 2rem;
}
.dropdown-content button{
    cursor: pointer;
    text-align: left;
    background-color: #ffffff;
    border: #ffffff;
    padding: 0.5rem 0.5rem 1rem;
    margin-left: -16%;
    width: 12rem;
    height: auto;
    font-family: 'Itim', cursive;
    font-size: 20px;
    color: #9A7B4F;
    text-decoration: none;
    transition: 0.6s;
}
.dropdown-content button:hover{
    cursor: pointer;
    background-color: #06D85F;
    border: 2px solid #06D85F;
    color: white;
    border-radius: 1rem;
}
.dropdown-content button:hover .icon{
    background-color: white;
    border: 2px solid  white;
    color: #06D85F;
}

.dropdown-content hr{
    margin-left: 1rem;
}

.navbar-main .dropdown{
    position: relative;
    display: inline-block;
    margin-left: 0;
}

.navbar-main .dropdown{
    float: right;
}

.dropdown:hover .dropdown-content {display: block;}
.dropdown:hover .menu-bars{
    color: #06D85F;
}
.dropdown:hover .dropdown-button {
    background-color: #ffffff;
    border: 2px solid #06D85F;
    padding: 0.5rem 0.5rem 2rem ;
    margin: 2rem 0 0;
}
.dropdown:hover .block{
    background-color: #ffffff;
    position: absolute;
    margin: -1.2rem 0 0 0.15rem;
    width: 3rem;
    height: 1.2rem;
    z-index: 100;
}


.overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 500ms;
    visibility: hidden;
    opacity: 0;
    z-index: 1000;
}
.overlay:target {
    visibility: visible;
    opacity: 1;
}

.popup {
    font-family: 'Itim', cursive;
    margin: 70px auto;
    background: rgb(229, 253, 255);
    padding: 20px;
    border-radius: 2rem;
    width: 40%;
    position: relative;
    transition: all 5s ease-in-out;
}

.popup .close {
    position: absolute;
    top: 20px;
    right: 30px;
    transition: all 200ms;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    color: #06D85F;
}
.popup .close:hover {
    color: #000000;
}
.popup .content {
    max-height: 80%;
    overflow: auto;
    z-index: 100;
    font-family: 'Itim', cursive;
}
.popup .content h1,
.popup .content h2,
.popup .content h3,
.popup .content h4
{
    padding: 1rem 0;
    text-align: center;
    color: rgb(40, 13, 95);
    font-weight: 600;
    font-family: 'Itim', cursive;
}
.popup span{
    color: #06D85F;
    font-weight: 600;
}

