.About-our .right .explain p{
    font-size: 24px;
    font-family: 'Itim', cursive;
}
.About-our .right .explain button{
    font-family: 'Caveat', cursive;
    padding: 1% 3%;
    border: 2px solid #06D85F;
    background-color: #06D85F;
    border-radius: 20px;
    color: #ffffff;
    cursor: pointer;
    text-align: center;
    font-size: 28px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    margin: 0 0 0 4%;
}
.About-our .right .explain button:hover{
    border: 2px solid #06D85F;
    background-color: #ffffff;
    color: #06D85F;
    padding: 2% 4%;
    transition: 0.8s ease-in-out;
}
.About-our .usage{
    font-family: 'Caveat', cursive;
    font-size: 36px;
    margin: 8% 0 4% 8%;
    text-align: left;
}
.About-our .image-usage{
    display: flex;
    flex-direction: row;
}
.About-our .image-usage img{
    margin: 2% 4% 0;
    padding: 2%;
    width: 120px;
    height: 120px;
    background-color: #ffffff;
    border: 2px solid #06D85F;
    border-radius: 20px;
}
.image-usage img:hover{
    background-color: rgb(229, 253, 255);
    border: 2px solid #06D85F;
    border-radius: 20px;
}
.About-our span{
    color: #06D85F;
}
